.Navbar {
  background-color: rgb(38, 207, 179);
  border-bottom: 5px solid rgb(212, 10, 94);
}
input[type="submit"] {
  color: white;
  background-color: green;
  width: 150px;
  height: 50px;
  outline: transparent;
  border-radius: 15px;
}
input[type="reset"] {
  color: white;
  background-color: rgb(218, 12, 12);
  width: 150px;
  height: 50px;
  outline: transparent;
  border-radius: 15px;
}
.Button {
  max-width: 40%;
  display: flex;
  justify-content: space-between;
  text-align: center;
}
label {
  font-weight: bold;
}
hr {
  border: 4px solid rgba(173, 3, 83, 0.959);
}
